import { Component, Prop, Vue } from "vue-property-decorator";


import { GstConfiguration } from "@/dto/GstConfiguration";
import AddressDetails from '@/components/Address/AddressDetails.vue';
import { FirestoreService } from "@/services/FirestoreService";
import { AuditFields } from "@/dto/AuditFields";
import { auth, storage } from "@/firebaseInit";

@Component({
  components: {
    AddressDetails
  }
})
export default class GstConfig extends Vue {
  @Prop({ required: false, default: function () { return new GstConfiguration() } })
  gstConfig!: GstConfiguration;
  @Prop({ required: false, default: false })
  edit!: boolean;

  @Prop({ required: false, default: "GST Setting" })
  label!: string;

  loading = false
  getGstDetailLoading = false;
  validEmail = [
    (v: any) => !!v || 'Email Id is required',
    (v: any) => /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(v) || 'Enter Valid Email Id',]
  uploadHeaderBtnDisabled = true;
  uploadHeaderBtnLoading = false;
  uploadSignBtnDisabled = true;
  uploadSignBtnLoading = false;
  selectedImage: any = null;
  selectedImageSign: any = null;

  selectedSignImage = '';

  selectedHeaderImage = '';
  async mounted() {
    await this.getGstDetails();
    if (this.gstConfig?.gstAddress?.stateCode !== '') {
      this.edit = true;
    } else {
      this.edit = false;
    }
    this.selectedHeaderImage = this.gstConfig.logoUrl;
    this.selectedSignImage = this.gstConfig.authSignUrl;

  }

  async validateForm() {
    if ((this.$refs.gstForm as Vue & { validate: () => boolean }).validate()) {
      await this.updateGstDetails();
    }
  }
  previewImage(event: any) {
    this.selectedImage = event.target.files[0];
    this.selectedHeaderImage = URL.createObjectURL(this.selectedImage);
    this.uploadHeaderBtnDisabled = false;
  }

  async uploadHeaderImage() {
    try {
      this.uploadHeaderBtnLoading = true;
      const fileExtension = this.getFileExtension(this.selectedImage);
      const resp = await storage.ref(`gstBilling/logo.` + fileExtension).put(this.selectedImage);
      const url = await resp.ref.getDownloadURL();
      this.gstConfig.logoUrl = url;
      Vue.toasted.show('Image Uploaded Successfully', { icon: 'face', type: 'success' });
      this.uploadHeaderBtnDisabled = true;
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });

    } finally {
      this.uploadHeaderBtnLoading = false;
    }


  }
  previewSignImage(event: any) {
    this.selectedImageSign = event.target.files[0];
    this.selectedSignImage = URL.createObjectURL(this.selectedImageSign);
    this.uploadSignBtnDisabled = false;
  }
  async uploadSignImage() {
    try {
      this.uploadSignBtnLoading = true;
      const fileExtension = this.getFileExtension(this.selectedImageSign);
      const resp = await storage.ref(`gstBilling/sign.` + fileExtension).put(this.selectedImageSign);
      const url = await resp.ref.getDownloadURL();
      this.gstConfig.authSignUrl = url;
      Vue.toasted.show('Image Uploaded Successfully', { icon: 'face', type: 'success' });
      this.uploadSignBtnDisabled = true;
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });

    } finally {
      this.uploadSignBtnLoading = false;
    }


  }
  getFileExtension(selectedImage: any) {
    return selectedImage.name.substring(selectedImage.name.lastIndexOf(".") + 1);
  }

  async updateGstDetails() {
    try {
      this.loading = true;

      const dateNow = new Date();
      const auditfield: AuditFields = {
        createdBy: { uid: auth.currentUser?.uid, name: auth.currentUser?.displayName, email: auth.currentUser?.email },
        createdOn: dateNow.getTime()
      }
      this.gstConfig.auditTrail = auditfield;
      const dbResp = await FirestoreService.setOrMergeDocument('setting', 'GSTInfo', this.gstConfig, GstConfiguration.converter);
      Vue.toasted.show('GST details updated Successfully', { icon: 'face', type: 'success' });

    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });

    } finally {
      this.loading = false;
    }
  }
  async getGstDetails() {
    try {
      this.getGstDetailLoading = true;
      const snapshot = await FirestoreService.getDocument('setting', 'GSTInfo', GstConfiguration.converter);
      if (snapshot !== null) {
        this.gstConfig = snapshot.data() as GstConfiguration;
      } else {
        Vue.toasted.show('No Saved Details found', { icon: 'face', type: 'error' });
      }


    } catch (error) {
      Vue.toasted.show('Error fetching GST Details', { icon: 'face', type: 'error' });
      console.log(error)

    }
    finally {
      this.getGstDetailLoading = false;
    }

  }
}
